* {
    padding: 0;
    outline: none;
  }
  
  body {
    /* font-family: 'Roboto', sans-serif; */
    background: #ffffff;
  }
  
  .containerCa {
    width: 280px;
    height: 500px;
    margin: 30px auto;
  }
  
  .card {
    border-radius: 25px;
    box-shadow: -11px 11px 1px rgba(0, 0, 0, 0.3);
  }
.icononavH {
  margin-top: 10px;
  padding-bottom: 10px;
  margin-bottom: -13px;
}
  
  .card-head {
    position: relative;
    height: 252px;
    background: #fa782e;
    /* Old browsers */
    background: -moz-linear-gradient(-45deg, #fa782e 8%, #c82930 83%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, #fa782e 8%, #c82930 83%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, #fa782e 8%, #c82930 83%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#fa782e', endColorstr='#c82930', GradientType=1);
    /* IE6-9 fallback on horizontal gradient */
    border-radius: 25px 25px 0 0;
  }
  
  .card-logo {
    width: 55px;
    margin: 20px;
  }
  
  .product-img {
    position: absolute;
    /* left: 0; */
    margin-top: -16px;
    /* margin-left: 50px; */
  }
  
  .product-detail {
    padding: 0 20px;
    font-size: 11px;
    color: #fff;
    width: 172px;
  }
  
  .product-detail h2 {
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 2px;
    /* padding-bottom: 10px; */
    text-transform: uppercase;
  }
  
  .back-text {
    display: inline-block;
    font-size: 100px;
    font-weight: 900;
    margin-left: -7px;
    margin-top: -83px;
    opacity: 0.1;
  }
  
  .card-body {
    height: 155px;
    background: #fff;
    border-radius: 0 0 25px 25px;
  }
  
  .product-title {
    padding: 20px 20px 5px 20px;
    display: block;
    font-size: 17px;
    font-weight: 500;
    letter-spacing: 1px;
    text-transform: uppercase;
  }
  
  .product-title b {
    font-weight: 900;
    letter-spacing: 0px;
  }
  
  .badge {
    position: relative;
    font-size: 10px;
    font-weight: 300;
    color: #fff;
    background: #11e95b;
    padding: 2px 5px;
    border-radius: 4px;
    top: -2px;
    margin-left: 5px;
  }
  
  .product-caption {
    display: block;
    padding: 0 20px;
    font-size: 10px;
    font-weight: 400;
    text-transform: uppercase;
  }
  
  .product-rating {
    padding: 0 20px;
    font-size: 11px;
  }
  
  .product-rating i.grey {
    color: #acacab;
  }
  
  .product-size h4 {
    font-size: 11px;
    padding: 0 21px;
    margin-top: 15px;
    padding-bottom: 10px;
    text-transform: uppercase;
  }
  
  .ul-size {
    margin-left: 15px;
  }
  
  .ul-size li {
    list-style: none;
    float: left;
    margin-right: 20px;
  }
  
  .ul-size li a {
    display: inline-block;
    text-decoration: none;
    font-size: 11px;
    width: 22px;
    height: 22px;
    border-radius: 100%;
    text-align: center;
    line-height: 23px;
    color: #000;
  }
  
  .ul-size li a.active {
    background: #f35e3d;
    color: #fff;
  }
  
  .product-size:before,
  .product-size:after {
    content: '';
    display: block;
    clear: both;
  }
  
  .product-color h4 {
    font-size: 11px;
    padding: 0 21px;
    margin-top: 20px;
    padding-bottom: 20px;
    text-transform: uppercase;
  }
  
  .ul-color {
    margin-left: 27px;
  }
  
  .ul-color li {
    list-style: none;
    float: left;
    margin-right: 20px;
  }
  
  .ul-color li a {
    display: inline-block;
    width: 16px;
    height: 16px;
    border-radius: 100%;
  }
  
  .ul-color li a.orange {
    background: #f35e3d;
  }
  
  .ul-color li a.green {
    background: #11e95b;
  }
  
  .ul-color li a.yellow {
    background: #ffd414;
  }
  
  .ul-color li a.active:after {
    position: absolute;
    content: '';
    display: inline-block;
    border: 1px solid #f35e3d;
    width: 24px;
    height: 24px;
    border-radius: 100%;
    margin-left: -5px;
    margin-top: -5px;
  }
  
  .product-price {
    position: absolute;
    background: #11e95b;
    padding: 7px 20px;
    text-align: center;
    display: inline-block;
    font-size: 24px;
    font-weight: 200;
    color: #fff;
    border-radius: 7px;
    margin-top: -13px;
    margin-left: 150px;
    box-shadow: -10px 20px 15px -10px rgba(17, 233, 91, 0.3);
    cursor: pointer;
  }
  
  .product-price b {
    margin-left: 5px;
  }
  
  @media screen and (max-width: 530px) {
    .containerCa {
      width: 140px;
      height: 500px;
      margin: 25px 16px;
    }
    .product-detail {
      padding: 0 20px;
      font-size: 6px;
      color: #fff;
      width: 115px;
      margin-left: -9px;
    }
    
    .product-detail h2 {
      font-size: 11px;
      font-weight: 500;
      letter-spacing: 2px;
      /* padding-bottom: 10px; */
      text-transform: uppercase;
      margin-bottom: 1px;
      /* margin-left: -9px; */
    }
    .icononavH {
      margin-left: -19px;
      margin-top: 10px;
      padding-bottom: 38px;
      margin-bottom: -51px;
    }
    .card-head {
      position: relative;
      height: 125px;
      border-radius: 25px 25px 0 0;
    }
    .card-body {
      height: 66px;
      background: #fff;
      border-radius: 0 0 25px 25px;
    }
    .back-text {
      display: inline-block;
      font-size: 60px;
      font-weight: 900;
      margin-left: -5px;
      margin-top: -93px;
      opacity: 0.1;
    }
    .product-img {
      position: absolute;
      left: 0;
      margin-top: -9px;
      margin-left: 55px;
    }
    .product-title {
    padding: 3px 0px 0px 9px;
    display: block;
    font-size: 10px;
    }
    .product-caption {
    padding: 0 9px;
    font-size: 7px;
    }
    .product-rating {
      padding: 0 9px;
      font-size: 7px;
    }
    .product-price {
      /* position: absolute;
      background: #11e95b; */
      padding: 5px 11px;
      /* text-align: center;
      display: inline-block; */
      font-size: 11px;
      font-weight: 200;
      border-radius: 7px;
      margin-top: -31px;
      margin-left: 63px;
    }
    .badge {
      position: sticky;
      font-size: 7px;
    }
  }